@font-face {
    font-family: cabin;
    src: url("./fonts/cabin-semibold.ttf");
}
.footer{
	background-color: #f2f5f8;
	color: #383737;
	vertical-align: middle;
	height: 170px;
	width: 100%;
	display: flex;
	align-items: center;
	font-family: cabin;
	word-spacing: 2px;
}

.footer a{
	text-decoration: none;
	color: #383737;
}

.footer-logo{
	margin-left: 120px;
	margin-right: 50px;
}

.footer-menu{
	width: 350px;
	display: flex;
	justify-content: space-between;
}
.footer__social-links{
	width: 95px;
	margin-left: 35px;
	display: flex;
	justify-content: space-between;
}
.address{
	margin-left: 200px;
}

.number,.street{
	color:#c8ced4;
}